.list {
  padding-left: 0;
  margin-bottom: 1.414rem;
  color: #000;
  list-style: none; }
  .list a {
    display: block;
    color: inherit; }

.list__item {
  position: relative;
  display: block;
  padding: 5px 15px;
  margin-bottom: -1px;
  border: 1px solid #000; }
  .list__item:first-child {
    border-top-left-radius: 3px;
    border-top-right-radius: 3px; }
  .list__item:last-child {
    margin-bottom: 0;
    border-bottom-left-radius: 3px;
    border-bottom-right-radius: 3px; }
  .list--arrows .list__item::after {
    position: absolute;
    top: 50%;
    right: 15px;
    font-size: 1.9994rem;
    color: #000;
    content: '›';
    transform: translateY(-50%); }
  .list--hover .list__item:hover {
    background-color: #676767; }

.table {
  width: 100%;
  max-width: 100%;
  margin-bottom: 1.414rem;
  border-radius: 3px; }

.table--bordered th, .table--bordered td {
  border: 1px solid #ccc; }

.table--bordered thead th {
  border-bottom: 2px solid #ccc; }

.table--underlined th, .table--underlined td {
  border-bottom: 1px solid #ccc; }

.table--padded th, .table--padded td {
  padding: 5px 10px; }

.table--striped tbody tr:nth-of-type(odd) {
  background-color: #010101; }

.table--hover tbody tr:hover {
  background-color: #020202; }

.table--responsive {
  display: block;
  width: 100%;
  min-height: 1px;
  overflow-x: auto; }

code,
pre {
  font-family: "Menlo", "Monaco", "Consolas", "Courier New", monospace; }

code {
  padding: .2rem .4rem;
  font-size: 90%;
  color: #000;
  background-color: rgba(0, 0, 0, 0.1);
  border-radius: 3px; }

pre {
  display: block;
  padding: 25px;
  overflow-x: scroll;
  background-color: #f7f7f7;
  border: 1px solid #ccc;
  border-radius: 3px; }
  pre code {
    padding: 0;
    background-color: transparent;
    border-radius: 0; }

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0; }

.sr-only-focusable:active,
.sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto; }

.visuallyhidden {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
  border: 0; }

.img--fluid {
  display: block;
  max-width: 100%;
  height: auto; }

.type--hide {
  font: "0/0" a;
  color: transparent;
  text-shadow: none;
  background-color: transparent;
  border: 0; }

.header--sm {
  font-size: 15px;
  text-transform: uppercase;
  letter-spacing: 1px; }

.clearfix::after {
  display: table;
  clear: both;
  content: ""; }
